import React from 'react';
import {Link} from 'react-router-dom';
import PropTypes from 'prop-types';

const LoseScreen = (props) => {
  const {
    timeOver,
    onRestartGame,
  } = props;

  return <section className="result">
    <div className="result__logo">
      <img src="img/melody-logo.png" alt=" " width="186" height="83"/>
    </div>

    <h2 className="result__title">{timeOver ? `  !` : ` !`}</h2>

    <p className="result__total result__total--fail">{timeOver ? ` !      ` : `    . ,    !`}</p>

    <Link to='/'>
      <button className="replay" type="button" onClick={onRestartGame}>  </button>
    </Link>
  </section>;
};

LoseScreen.propTypes = {
  timeOver: PropTypes.bool.isRequired,
  onRestartGame: PropTypes.func.isRequired,
};

export default LoseScreen;
